$ticket-wrapper-background: #fff;
$ticket-wrapper-box-shadow-color: rgba(0, 0, 0, 0.1);
$ticket-cost-color: #2196f3;
$ticket-label-color: #a0b0b9;
$ticket-text-color: #4a4a4a;

.wrapper {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  background: $ticket-wrapper-background;
  box-shadow: 0px 2px 8px $ticket-wrapper-box-shadow-color;
  border-radius: 5px;
  padding: 0 20px;
}

.cost {
  padding-top: 20px;
  grid-column: 1 / 3;
  font-weight: 600;
  font-size: 24px;
  line-height: 24px;
  color: $ticket-cost-color;
  height: 40px;

  @media (max-width: 419px) {
    padding-top: 20px;
    font-size: 20px;
    line-height: 20px;
  }
}

.logo {
  padding-top: 20px;
  width: 110px;
  height: 36px;

  @media (max-width: 419px) {
    width: 74px;
    height: 24px;
  }
}

.voyageCell {
  display: flex;
  flex-direction: column;
  justify-content: center;
  height: 40px;
  font-weight: 600;
  font-size: 12px;
  line-height: 18px;
  letter-spacing: 0.5px;
  text-transform: uppercase;

  @media (max-width: 419px) {
    font-size: 10px;
    line-height: 14px;
  }
}

.label {
  color: $ticket-label-color;
}

.text {
  font-size: 14px;
  line-height: 21px;
  color: $ticket-text-color;

  @media (max-width: 419px) {
    font-size: 10px;
    line-height: 17px;
  }
}

.duration {
  text-transform: none;
}
